import React, { useState, useEffect } from 'react';
// import Speech from 'react-speech';

import Stats from "./stats"
import { FaArrowAltCircleRight } from 'react-icons/fa';
import fail1 from "../data/fail/1.mp3"
import fail2 from "../data/fail/2.mp3"
import fail3 from "../data/fail/3.mp3"
import fail4 from "../data/fail/4.mp3"
import win1 from "../data/nice/1.mp3"
import win2 from "../data/nice/2.mp3"
import win3 from "../data/nice/3.mp3"

export default function Question({ ...data }) {
    const [isEnd, setEnd] = useState(false);
    const [isCorrect, setCorrect] = useState(false);
    const [chosenIdx, setChosenIdx] = useState(false);
    const [stats, setStats] = useState({ correct: 0, wrong: 0, questLeft: data.questLeft });
    const [isLock, setLock] = useState(false);
    const soundFail1 = new Audio(fail1);
    const soundFail2 = new Audio(fail2);
    const soundFail3 = new Audio(fail3);
    const soundFail4 = new Audio(fail4);
    const soundWin1 = new Audio(win1);
    const soundWin2 = new Audio(win2);
    const soundWin3 = new Audio(win3);



    function playSounds(type) {

        let num = Math.floor(Math.random() * 4)
        if (type === "win") {
            if (num > 4) num -= 1
            if (num === 0) num += 1
            eval(`soundWin${num}.play()`)

        } else {
            if (num > 3) num -= 1
            if (num === 0) num += 1
            eval(`soundFail${num + 1}.play()`)


        }
    }

    function onAnswer(idx) {
        if (isLock) return
        console.log('answerd: ', idx)
        const answer = data.data.quiz.options[idx].slice(0, 1)
        const correctAnswer = data.data.quiz.answers.slice(8, 9)
        setCorrect(getRightAnswerIdx(correctAnswer))
        let newStats = { ...stats }
        if (answer === correctAnswer) {
            playSounds("win")
            setChosenIdx(idx)
            newStats.correct += 1
        } else {
            playSounds("lose")
            setChosenIdx(idx)
            newStats.wrong += 1
        }
        setStats(newStats)
        setEnd(1)
        setLock(true)
        console.log('STATS: ', stats)
    }

    function getRightAnswerIdx(correctAnswer) {
        let correctIdx = false
        data.data.quiz.options.forEach((opt, idx) => {
            if (opt.slice(0, 1) === correctAnswer) {
                correctIdx = idx
            }
        })
        return correctIdx
    }
    function getBoxClass(idx) {
        if (!isEnd) return
        let res
        if (isCorrect === idx) {
            res = 'answer-box-correct jello-horizontal'
        } else if (idx === chosenIdx) {
            res = 'answer-box-wrong vibrate-1 '
        }
        if (chosenIdx === idx) {
            res += " highlight-chosen"
        }
        return res
    }
    function goNext() {
        data.cancelRobot()
        data.onGoNext()
        setEnd(false);
        setCorrect(false);
        setChosenIdx(false);
        setLock(false)
        const newStats = { ...stats }
        newStats.questLeft = data.questLeft
        setStats(newStats)

    }
    return (<>
        <Stats stats={stats}></Stats>
        <div className='questions'>
            {/* <Speech text={data.data.quiz.question} voice="Daniel" /> */}

            <h1 className="question-title">{data.data.quiz.question}</h1>
            <div className="answer-container">
                <div className="talker-container">

                    <button
                        type="button"

                        onClick={() => data.talkBitch()}
                        className="talk-btn"
                    >
                        Talk
              </button>
                    <button
                        type="button"
                        className="stfu-btn"
                        onClick={() => data.cancelRobot()}
                    >
                        Stfu
              </button>
                </div>




                <div className="answer-box-grid">
                    {data.data.quiz.options.map((opt, idx) => {
                        return (<div key={opt + idx} onClick={() => onAnswer(idx)} className={`answer-box ${getBoxClass(idx)}`}><p>{opt}</p></div>)
                    })}

                </div>
            </div>


        </div>
        <div className="next-question-btn" onClick={goNext}><FaArrowAltCircleRight></FaArrowAltCircleRight></div></>
    );
}
